import datastore from '@morphoto/datastore'

const getPhotoUrls = async (photo, sizePriority) => {
  try {
    // Fist see if we have anything in the indexdb
    if (photo.uploadToken) {
      try {
        const dbKeys = await datastore.offline.imgStore.keys()
        if (dbKeys.indexOf(photo.uploadToken) > -1) {
          const blob = await datastore.offline.imgStore.getItem(photo.uploadToken)
          // log.info('getPhotoUrls:: Found DB Entry. using data.file', blob data)
          const urlCreator = window.URL || window.webkitURL
          const imageUrl = urlCreator.createObjectURL(blob)
          return [imageUrl]
        }
      }
      // If anything in the retrieval fails just fall back
      catch {}
    }
    // now we return the remote URLS if we can
    return sizePriority.map(sp => `${photo.paths.base}${photo.paths[sp]}`)
  }
  catch (e) {
    return null
  }
}

export default getPhotoUrls
