import React, { useState, useMemo, useContext, useEffect } from 'react'
import PropTypes from 'prop-types'
import { graphql, compose } from 'react-apollo'
import ReactGA from 'react-ga'

import { connect } from 'react-redux'

import { config, AppContext } from '@morphoto/client-common'
import { graphqlAPI, dates } from '@morphoto/common'
import log from 'loglevel'

const TABS = config.home.SEARCH_TABS

const withSearchConnect = Wrapped => {
  const Inner = ({ defaultTab, defaultText, fetchMore }) => {
    // log.info('withSearchConnect', { Wrapped, defaultTab, defaultText, fetchMore })
    const [currentTab, setCurrentTab] = useState(defaultTab || TABS.TAG)
    const { globalActions } = useContext(AppContext)
    // We separate these because one of them needs to be immediate and the
    const [searchText, setSearchText] = useState(defaultText || '')
    const [contextChoice, setContextChoice] = useState(null)

    useEffect(() => {
      ReactGA.pageview('search')
    }, [])

    useEffect(() => {
      if (contextChoice || searchText.length > 3) {
        ReactGA.event({
          category: `Search:${currentTab}`,
          action: contextChoice ? 'Search Context Select' : 'Search Text Change',
          label: contextChoice ? contextChoice.name : searchText
        })
      }
    }, [searchText, currentTab, contextChoice])

    const handleTabChange = (newTab) => {
      setContextChoice(null)
      setCurrentTab(newTab)
    }
    const handleContextChoiceChange = (newContextChoice) => {
      setContextChoice(newContextChoice)
    }
    const handleSearchTextChange = (text) => {
      setSearchText(text)
      setContextChoice(null)
    }

    let withContextQuery = null
    let withSearchQuery = null

    switch (currentTab) {
      case TABS.KEYWORD:
        withContextQuery = (Wrapped) => Wrapped
        withSearchQuery = withKeywordSearch
        break
      case TABS.TAG:
        withContextQuery = withTagSearch
        withSearchQuery = withTagSeries
        break
      case TABS.LOCATION:
        withContextQuery = withGeoCode
        // withSearchQuery = withGeoSearch
        break
      case TABS.USER:
        withContextQuery = withUserSearch
        withSearchQuery = withUserSeries
        break
      default:
        break
    }

    const ComposedSearch = useMemo(() => withSearchQuery
      ? compose(
        withContextQuery,
        withSearchQuery
      )(Wrapped)
      : withContextQuery(Wrapped), [withContextQuery, withSearchQuery])

    return <ComposedSearch {...{ currentTab, searchText, contextChoice }}
      actions={{
        onMapClick: globalActions.jumpToMap,
        onResultClick: globalActions.jumpToSeriesView,
        setContextChoice: handleContextChoiceChange,
        fetchMore: fetchMore,
        setCurrentTab: handleTabChange,
        setSearchText: handleSearchTextChange
      }}
    />
  }
  Inner.propTypes = {
    searchResults: PropTypes.array,
    contextResults: PropTypes.array,
    defaultTab: PropTypes.any,
    defaultText: PropTypes.string,
    loading: PropTypes.bool,
    fetchMore: PropTypes.func
  }
  return Inner
}
/*****************************************************************
 *  PART 1: SEARCH QUERIES
 *****************************************************************/

/**
 * Here's the graphql HOC that gets an individual series
 */
const withTagSearch = graphql(graphqlAPI.queries.tagSearch, {
  props: ({ data }) => {
    if (data.error) log.error(data.error)
    // log.info('withTagSearch', data)
    let series = null
    try {
      if (!data.loading && !data.error) series = data.series
    }
    catch (e) {
      log.error(data)
      log.error(e)
    }
    const contextResults = data.tags ? data.tags.map(({ name, count }) => ({
      name,
      description: `~ ${count < 10 ? count : Math.ceil(count / 10) * 10} Photo series tagged`
    })) : []

    return {
      contextResults,
      loading: data.loading,
      series
    }
  },
  // skip: ({ searchText }) => !searchText || searchText.length < 3,
  options: ({ searchText }) => ({
    variables: {
      tag: !searchText || searchText.length < 3 ? '' : searchText,
      limit: 10
    }
  })
})

const withUserSearch = graphql(graphqlAPI.queries.userSearch, {
  props: ({ data }) => {
    if (data.error) log.error(data.error)
    // log.info('withUserSearch', data)
    let series = null
    try {
      if (!data.loading && !data.error) series = data.series
    }
    catch (e) {
      log.error(data)
      log.error(e)
    }

    const contextResults = data.userSearch ? data.userSearch.map(({ username, gravatar, createdOn, id }) => ({
      name: username,
      id,
      description: `Joined ${dates.formatDateAgo(createdOn)}`,
      gravatar
    })) : []

    return {
      contextResults,
      loading: data.loading,
      series
    }
  },
  skip: ({ searchText }) => !searchText || searchText.length < 3,
  options: ({ searchText }) => ({
    variables: {
      searchStr: searchText,
      limit: 10
    }
  })
})

