import MyFeed from '@morphoto/feed'
import { UploadControl } from '@morphoto/ui'
import Search from '@morphoto/search'
import MyProfile from '@morphoto/profile'
import { HomeMap, OfflineMap } from '@morphoto/maps'
import PhotoSeries from '@morphoto/photoseries'

import ErrorConnect from './Error.connect'
// import { IntlWrapper } from '../translations'
import Home from '@morphoto/home'
import log from 'loglevel'

const { home: homeConfig, tagColors } = config

// Analytics.configure(config.aws)
// Analytics.record('some-event-name')

// Set up our google analytics
const gac = {}
if (process.env.NODE_ENV !== 'production') {
  gac.debug = false
  gac.gaOptions = {
    cookieDomain: 'none',
    siteSpeedSampleRate: 100
  }
}
// log.info('ReactGA.initialize', gac)
ReactGA.initialize(config.analytics.token, gac)

const components = {
  upload: withUploadConnect(UploadControl),
  series: withSeriesConnect(PhotoSeries),
  map: withHomeMap(HomeMap),
  search: withSearchConnect(Search),
  profile: withProfileConnect(MyProfile),
  feed: withFeedConnect(MyFeed),
  offline: withOfflineData(OfflineMap)
}

/**
 * HOC for AppContext and ApolloProvider
 */
export const withApolloAuthProvider = Wrapped => {
  const AuthProvider = ({ reduxProps, ...reduxActions }) => {
    const flags = reduxProps.flags
    // log.info('withApolloAuthProvider', props)
    const { isGod, signedIn, authData, signOut, authScreen, forceSignIn } = useAuthentication(flags.useLoggedOut, reduxActions.setUseOffline)
    const { client: apolloClient, done: apolloDone, currState: apolloState } = datastore.graphql.useGraphQL()

    // This triggers all the PWA Goodness
    const { installPrompt, addToHomeScreen } = useShowAddToHome(flags.install, reduxActions.dismissInstall)

    // A little tracking goodness
    useEffect(() => {
      ReactGA.pageview('initial render')
    }, [])

    // If the user hasn't decided to be anonymous or sign in yet show them the login screen
    // log.info('Rendering AppConnect', { flags, responsive, props, signedIn, isGod, authData })
    useEffect(() => {
      if (signedIn === true && flags.useLoggedOut) reduxActions.setUseOffline(false)
      else if (signedIn === false && !flags.useLoggedOut) reduxActions.setUseOffline(true)
    }, [flags.useLoggedOut, reduxActions, signedIn])

    const retryRef = useRef()

    // We need to hijack the browser's history object so that hitting the back button on android
    // doesn't close the window
    useEffect(() => {
      function handleBackClick () {
        window.history.pushState({}, '')
        reduxActions.onMenuBack()
      }
      window.addEventListener('popstate', handleBackClick)
      return () => window.removeEventListener('popstate', handleBackClick)
    }, [reduxActions])

    // One time only query param parse so we can redirect them to a shared url if necessary
    useEffect(() => {
      try {
        const decodedObj = decodeShareUrl(window.location.search)
        if (decodedObj && decodedObj.type && decodedObj.s) {
          const { type, s, p } = decodedObj
          log.info('REDIRECTING', { type, s, p })
          ReactGA.event({
            category: 'user',
            action: 'share',
            label: window.location.search
          })
          switch (type) {
            case 'series':
              const queryVars = { activeSeries: s, initialPhoto: p }
              if (!signedIn) reduxActions.setUseOffline(true)
              reduxActions.setHomeState(homeConfig.AppStateTypes.SERIES, queryVars)
              break
            default:
              break
          }
        // TODO : I think this breaks the maifest file
        // window.history.replaceState({}, 'MorPhoto', '/')
        }
      }
      catch (e) {
        log.error(e)
      }

      //  clean up our retry ref
      if (retryRef.current) {
        clearTimeout(retryRef.current)
        retryRef.current = null
      }
    }, [reduxActions, signedIn])

    const forceSignInWrapped = useCallback(() => {
      // Analytics.record('FORCE_SIGNIN')
      ReactGA.event({
        category: 'User',
        action: 'FORCE_SIGNIN'
      })
      reduxActions.setUseOffline(false)
      forceSignIn()
    }, [reduxActions, forceSignIn])

    const resetApp = useCallback(() => {
      // Analytics.record('RESET_APP')
      log.info('APP RESET STARTING')
      ReactGA.event({
        category: 'User',
        action: 'RESET_APP'
      })
      datastore.offline.imgStore.clear()
      apolloClient.resetStore()
      datastore.offline.stateStore.clear()
      reduxActions.resetAllState()
      signOut()
      log.info('APP RESET FINISHED')
    }, [apolloClient, signOut, reduxActions])

    const staticContext = useMemo(() => ({
      metaConfig: {},
      tagConfig: [
        {
          id: 'freeBothType',
          label: '',
          color: tagColors[0],
          allowXY: true,
          allowNonXY: true,
          allowAdditions: true,
          values: getSeriesTags()
        }
      ]
    }), [])

    // These are the things the rest of the app needs to find:
    const appContext = {
      getPhotoUrls,
      signedIn: Boolean(signedIn),
      role: !signedIn
        ? 'nonuser'
        : isGod
          ? 'admin'
          : 'user',
      cognitoUser: authData, // TODO: Need this supported by a graphql query
      globalActions: {
        ...reduxActions,
        addToHomeScreen,
        signOut,
        forceSignIn: forceSignInWrapped,
        resetApp
      },
      ...staticContext
    }

    /**
     * NO HOOKS BELOW HERE
     *
     *
     *
     */

    if (!apolloDone) return <Loading message={datastore.graphql.APOLLOSTATES[apolloState]} />

    if (signedIn === null && !flags.useLoggedOut) {
      ReactGA.event({
        category: 'User',
        action: 'LOGIN_PROMPT'
      })
      return authScreen
    }

    return (
    // We wrap the outside in an error handler
      <ApolloProvider client={apolloClient}>
        <Query query={graphqlAPI.queries.getUser} skip={!signedIn}>
          {({ loading, error, data, refetch }) => {
            if (loading) return <Loading message='UserData' />
            if (error) {
              log.error(error)
            }
            // If there's no record for this user in the DB there's nothing left to do
            if (signedIn && (!data || !data.user)) {
              log.info('NO USER DATA YET', { loading, error, data })
              if (!retryRef.current) {
                retryRef.current = setTimeout(() => {
                  log.info('Retrying user query')
                  refetch()
                }, 5000)
              }
              return (
                <Loading message='UserData' />
              )
            }
            // If we've changed users then wipe the old user's stuff
            else if (signedIn && authData.attributes.sub !== data.user.id) {
              log.info('USER CHANGE')
              datastore.offline.imgStore.clear()
              apolloClient.resetStore()
              datastore.offline.stateStore.clear()
              reduxActions.resetAllState()
            }
            else if (signedIn) appContext.currentUser = data.user

            components.installPrompt = installPrompt

            return (
              <AppContextProvider value={appContext}>
                <Wrapped domain={reduxProps.domain} components={components} />
              </AppContextProvider>
            )
          }}
        </Query>
      </ApolloProvider>
    )
  }
  AuthProvider.propTypes = {
    reduxProps: PropTypes.object,
    reduxActions: PropTypes.object
  }
  return AuthProvider
}

/**
 * Map our redux state and functions
 */
const mapStateToProps = (state, props) => {
  const { domain } = state.home.current
  return {
    reduxProps: {
      domain,
      flags: state.flags
    }
  }
}

const mapDispatchToProps = dispatch => ({
  setHomeState: (newDomain, newParams) => {
    dispatch(datastore.redux.actions.setHomeState(newDomain, newParams))
  },
  onCameraClick: () => {
    dispatch(datastore.redux.actions.setHomeState(homeConfig.AppStateTypes.NEW_SERIES_CAMERA, { activeSeries: null }))
  },
  onLibraryClick: () => {
    dispatch(datastore.redux.actions.setHomeState(homeConfig.AppStateTypes.NEW_SERIES_LIBRARY, { activeSeries: null }))
  },
  onOfflineMenuClick: () => {
    dispatch(datastore.redux.actions.setHomeState(homeConfig.AppStateTypes.OFFLINE_DOWNLOAD))
  },
  onSettingsClick: () => {
    dispatch(datastore.redux.actions.setHomeState(homeConfig.AppStateTypes.SETTINGS))
  },
  jumpToSeriesView: (seriesId, photoId) => {
    dispatch(datastore.redux.actions.setHomeState(homeConfig.AppStateTypes.SERIES, { activeSeries: seriesId, initialPhoto: photoId }))
  },
  jumpToUser: (username) => {
    dispatch(datastore.redux.actions.setHomeState(homeConfig.AppStateTypes.SEARCH, { search: { tab: config.home.SEARCH_TABS.USER, text: username } }))
  },
  jumpToMap: ({ bbox, center }) => {
    let bounds = bbox
    if (!bounds && center) {
      const bufferPts = buffer(point(center), 500, { units: 'meters' })
      bounds = bboxFunc(bufferPts)
    }

    if (bounds) {
      dispatch(datastore.redux.actions.pushMapHistory({
        mode: config.home.MapModeTypes.FREE,
        bounds: bounds
      }))
      dispatch(datastore.redux.actions.setHomeState(homeConfig.AppStateTypes.MAP, { activeSeries: null, initialPhoto: null }))
    }
  },
  jumpToSeriesMap: (seriesId, photoId) => {
    dispatch(datastore.redux.actions.pushMapHistory({
      mode: config.home.MapModeTypes.FREE
    }))
    dispatch(datastore.redux.actions.setHomeState(homeConfig.AppStateTypes.MAP, { activeSeries: seriesId, initialPhoto: photoId }))
  },
  resetAllState: () => {
    dispatch(datastore.redux.actions.resetAllState())
  },
  dismissInstall: () => {
    dispatch(datastore.redux.actions.setFlag('install', false))
  },
  setUseOffline: (bVal) => {
    dispatch(datastore.redux.actions.setFlag('useLoggedOut', bVal))
  },
  onQRButtonClick: () => {
    dispatch(datastore.redux.actions.setHomeState(homeConfig.AppStateTypes.QRCAPTURE))
  },
  onMenuBack: () => {
    dispatch(datastore.redux.actions.homeStateBack())
  },
  onAvatarClick: (username) => {
    dispatch(datastore.redux.actions.setHomeState(homeConfig.AppStateTypes.SEARCH, {
      search: { tab: homeConfig.SEARCH_TABS.USER, text: username }
    }))
  },
  onQRCapture: (type, activeSeries, initialPhoto) => {
    switch (type) {
      case 'series':
        dispatch(datastore.redux.actions.setHomeState(homeConfig.AppStateTypes.SERIES, { activeSeries, initialPhoto }))
        break
      default:
        break
    }
  }
})

const withErrorBoundary = (Wrapped) => (props) => (
  <ErrorBoundary fallback={ErrorConnect}>
    <Wrapped {...props} />
  </ErrorBoundary>
)

