import React, { useEffect, useState, useRef, useMemo } from 'react'
import PropTypes from 'prop-types'

import { connect } from 'react-redux'
import { graphql, compose } from 'react-apollo'

import datastore from '@morphoto/datastore'
import { graphqlAPI } from '@morphoto/common'
import { config } from '@morphoto/client-common'
import log from 'loglevel'

const POLL_INTERVAL = 20 // (seconds)

/**
 * The outer component handles the lastCheck variable which, when changed will
 * cause the poll event
 */
const withHomeMap = Wrapped => {
  const InnerHomeMap = ({
    onSeriesNavigate,
    onSeriesMapSelect,
    onSearchClick,
    popMapHistory,
    pushMapHistory,
    clearMapHistory,
    initialActiveSeries,
    initViewport
  }) => {
    const [seriesProps, setSeriesProps] = useState()
    const [queryVersion, setVersion] = useState(0)
    const timerRef = useRef()
    const queryVersionRef = useRef()
    const [lastCheck, setLastCheck] = useState(0)
    queryVersionRef.current = queryVersion

    // These are expensive queries so we render them memoized to prevent erroneous re-renders
    const allSeriesQUERY = useMemo(() => <AllSeriesConnect setSeriesProps={setSeriesProps} />, [])
    const pollSeriesQUERY = useMemo(
      () => <PollQueryConnect lastCheck={lastCheck} seriesProps={seriesProps} updateIncrement={() => setVersion(queryVersionRef.current + 1)} />,
      [lastCheck, seriesProps]
    )
    // log.info('withPollQuery (PARENT)', { lastCheck, seriesProps })
    // Initiate the polling process. We simply update the timestamp every N seconds
    useEffect(() => {
      if (!seriesProps) return
      function ping () {
        if (timerRef.current) setLastCheck(String(Date.now() - POLL_INTERVAL * 1000))
      }
      if (!timerRef.current && seriesProps.readyForPolling) timerRef.current = setInterval(ping, POLL_INTERVAL * 1000)
      return () => {
        if (timerRef.current) {
          log.info('clear poll timer')
          clearInterval(timerRef.current)
          timerRef.current = null
        }
      }
    }, [seriesProps])
    // log.info('withHomeMap', { seriesProps, queryVersion })

    return (
      <React.Fragment>
        {allSeriesQUERY}
        {pollSeriesQUERY}
        <Wrapped
          initialActiveSeries={initialActiveSeries}
          initViewport={initViewport}
          actions={{
            onSeriesNavigate,
            onSeriesMapSelect,
            onSearchClick,
            popMapHistory,
            pushMapHistory,
            clearMapHistory
          }}
          queryVersion={queryVersion}
          seriesList={seriesProps && seriesProps.allSeries ? seriesProps.allSeries.seriesSummaries : []}
          loading={!seriesProps || seriesProps.loading}
        />
      </React.Fragment>
    )
  }
  InnerHomeMap.propTypes = {
    onSeriesNavigate: PropTypes.func.isRequired,
    onSeriesMapSelect: PropTypes.func.isRequired,
    onSearchClick: PropTypes.func.isRequired,
    popMapHistory: PropTypes.func.isRequired,
    pushMapHistory: PropTypes.func.isRequired,
    clearMapHistory: PropTypes.func.isRequired,
    initialActiveSeries: PropTypes.string,
    initViewport: PropTypes.object
  }
  // InnerHomeMap.whyDidYouRender = true
  return InnerHomeMap
}

const withAllSeries = graphql(graphqlAPI.queries.allSeries, {
  props: ({ data, ownProps }) => {
    // log.info('withAllSeries', { data, ownProps })
    // If there's more data to load we should go get another batch
    if (!data.loading && !data.error && data.allSeries && data.allSeries.seriesSummaries.length > 0 && data.allSeries.nextToken) {
      data.fetchMore({
        variables: { ...data.variables, nextToken: data.allSeries.nextToken },
        // How do we mush the new query with the old one
        updateQuery: (prev, { fetchMoreResult }) => {
          return {
            allSeries: {
              ...fetchMoreResult.allSeries,
              seriesSummaries: [ ...prev.allSeries.seriesSummaries, ...fetchMoreResult.allSeries.seriesSummaries ]
            }
          }
        }
      })
    }
    else if (data.loading || data.error) {
      ownProps.setSeriesProps(data)
    }
    else {
      // log.info('withAllSeries polling done')
      ownProps.setSeriesProps({ ...data, readyForPolling: true })
    }
    return null
  },
  options: {
    variables: { limit: 100 }
  }
})

/**
 * Here's the graphql HOC that gets an individual series
 */
const withPollQuery = graphql(graphqlAPI.queries.pollQuery, {
  skip: ({ lastCheck, online }) => !lastCheck || !online,
  props: ({ data, ownProps: { seriesProps, updateIncrement } }) => {
    if (data.error) {
      log.info('POLL FAIL', data.error, data)
      log.error(data.error)
    }
    else {
      // log.info('withPollQuery', { data, seriesProps, updateIncrement })
    }
    if (!data.loading && data.changedSeries && data.changedSeries.seriesSummaries) {
      // log.info('POLLING', { data, seriesProps })
      const newData = data.changedSeries.seriesSummaries
      const oldList = seriesProps.allSeries.seriesSummaries

      // If the polling has begun and we have some good polling data back then
      // Process it and decide if we need to incorporate it
      if (data.changedSeries) {
        let newSeriesSummaries = seriesProps.allSeries.seriesSummaries
        newData.forEach(({ seriesStatus, ...polledSeries }) => {
          if (seriesStatus === 'deleted') {
            log.info('Polling found a series to delete: ', polledSeries.id)
            newSeriesSummaries = oldList.filter(s => s.id !== polledSeries.id)
          }
          else {
            const idx = oldList.findIndex(ds => ds.id === polledSeries.id)
            if (idx >= 0) {
              log.info('Polling found a series to Update: ', polledSeries.id)
              newSeriesSummaries = oldList.map((oldSeries, oldId) => oldId === idx ? polledSeries : oldSeries)
            }
            else {
              log.info('Polling found a series to add: ', polledSeries.id)
              newSeriesSummaries = [...oldList, polledSeries]
            }
          }
          seriesProps.updateQuery((data, options) => {
            try {
              const newData = { ...data }
              newData.allSeries.seriesSummaries = newSeriesSummaries
              updateIncrement()
              return data
            }
            catch (e) {
              log.error(e)
              return data
            }
          })
        })
      }
    }
    return null
  },
  options: ({ lastCheck }) => ({
    variables: {
      limit: 100,
      lastCheck
    }
  })
})

// We're using graphql() HOC because it's convenient but it doesn't need to render anything
const AllSeriesConnect = withAllSeries(() => null)
const PollQueryConnect = withPollQuery(() => null)

// The only part of the state the main view caes about is teh window
const mapStateToProps = (state, props) => {
  const { current: { activeSeries } } = state.home
  const initViewport = state.map.slice(-1).pop()
  return { initialActiveSeries: activeSeries, initViewport, ...props }
}

const STATES = config.home.AppStateTypes

// There are a few presentational actions the main window can use
const mapDispatchToProps = {
  onSeriesNavigate: activeSeries => datastore.redux.actions.setHomeState(STATES.SERIES, { activeSeries }),
  onSeriesMapSelect: datastore.redux.actions.setActiveSeries,
  onSearchClick: () => datastore.redux.actions.setHomeState(STATES.SEARCH, { search: { tab: config.home.SEARCH_TABS.LOCATION, text: '' } }),
  popMapHistory: datastore.redux.actions.popMapHistory,
  pushMapHistory: datastore.redux.actions.pushMapHistory,
  clearMapHistory: datastore.redux.actions.clearMapHistory
}

export default compose(
  connect(
    mapStateToProps,
    mapDispatchToProps
  ),
  withHomeMap
)
