import React, { useContext, useMemo, useEffect } from 'react'
import PropTypes from 'prop-types'
import { graphql, compose } from 'react-apollo'
import { connect } from 'react-redux'
import ReactGA from 'react-ga'

import { NEW_PHOTO_MODES, SERIES_MODES } from '@morphoto/photoseries'
import { config, AppContext } from '@morphoto/client-common'
import datastore from '@morphoto/datastore'
import { graphqlAPI } from '@morphoto/common'
import log from 'loglevel'

const STATES = config.home.AppStateTypes

const withSeriesConnect = WrappedSeries => {
  const InnerSeries = ({
    series, loading, domain, activeSeries, initialPhoto,
    onAfterSeriesDelete, queueUpload,
    updateSeriesMutation, updatePhotoMutation,
    deleteSeriesMutation, deletePhotoMutation,
    createSeriesMutation, createPhotoMutation,
    watchSeriesMutation, unWatchSeriesMutation,
    onFindOnMap
  }) => {
    const { role, currentUser, signedIn, globalActions } = useContext(AppContext)

    const inputMethod = domain === STATES.NEW_SERIES_CAMERA
      ? NEW_PHOTO_MODES.CAMERA
      : NEW_PHOTO_MODES.FILEPICKER

    // log.info('SeriesConnect', { series, loading, currentState, activeSeries, role, currentUser })
    let modeName = SERIES_MODES.SERIES_VIEW
    if (domain !== STATES.SERIES) modeName = SERIES_MODES.NEWSERIES

    // A little mutation here.
    useMemo(() => {
      if (series && series.photos) {
        series.photos.sort((a, b) => parseInt(a.takenOn, 10) - parseInt(b.takenOn, 10))
      }
    }, [series])

    useEffect(() => {
      if (series && series.id) {
        ReactGA.event({
          category: `Series`,
          action: 'View',
          label: series.id
        })
      }
    }, [series])

    let owned = false
    try {
      owned = currentUser.id === series.createdUser.id
    }
    catch (e) {}
    const watching = signedIn && currentUser && currentUser.watching && currentUser.watching.indexOf(activeSeries) > -1

    return (
      <WrappedSeries
        modeName={modeName}
        inputMethod={inputMethod}

        owned={owned}
        watching={watching}

        // Everything we need to determine access
        currentUserId={signedIn ? currentUser.id : null}
        currentUserRole={role}
        loading={loading}
        series={series}
        initialPhoto={initialPhoto}

        actions={{
          // Now some functions to handle actions we might take
          onNewPhoto: async (newPhoto, newPhotoData) => {
            log.info('onNewPhoto::', { series, newPhoto, newPhotoData })
            // Write this blob to the imgStore so we can use it later
            log.info('SET ITEM', newPhotoData)
            await datastore.offline.imgStore.setItem(newPhoto.uploadToken, newPhotoData.file)
            return createPhotoMutation(series, newPhoto, newPhotoData.ext)
              .then(({ data }) => {
                // Now we can add this photo to the upload queue
                const photo = data.createPhoto.photo
                if (data.createPhoto.preAuth) {
                  const preAuth = JSON.parse(data.createPhoto.preAuth)

                  queueUpload({
                    id: photo.id,
                    uploadToken: photo.uploadToken,
                    seriesId: photo.seriesId,
                    expires: Date.now() + (3300 * 1000),
                    preAuth,
                    ext: newPhotoData.ext,
                    retry: 0
                  })
                }
              })
              .catch(err => {
                log.error('onNewPhoto:: Graphql Error', err)
              })
          },

          onChangePhoto: (photo, photoDelta) => updatePhotoMutation(series, photo, photoDelta)
            .catch(err => {
              log.error('onChangePhoto:: Graphql Error', err)
            }),

          onDeletePhoto: (photo) => deletePhotoMutation(photo.id)
            .then((...data) => {
              log.info('onDeletePhoto:: photo deleted', data)
            })
            .catch(err => {
              log.error('onDeletePhoto:: Graphql Error', err)
            }),

          onChange: (series, seriesDelta) => updateSeriesMutation(series, seriesDelta)
            .catch(err => {
              log.error('onChange:: Graphql Error', err)
            }),

          onNewSeries: async (newSeries, newPhoto, newPhotoData) => {
            log.info('Series.connect:: onNewSeries starting', { newSeries, newPhoto, newPhotoData })
            log.info('SET ITEM', newPhotoData)
            await datastore.offline.imgStore.setItem(newPhoto.uploadToken, newPhotoData.file)
            return createSeriesMutation(newSeries, newPhoto, newPhotoData.ext)
              .then(async ({ data }) => {
                log.info('Series.connect::', data)
                if (data.createSeries.preAuth) {
                  // Now we can add this photo to the upload queue
                  const photo = data.createSeries.series.photos[0]
                  const preAuth = JSON.parse(data.createSeries.preAuth)

                  queueUpload({
                    id: photo.id,
                    uploadToken: photo.uploadToken,
                    seriesId: photo.seriesId,
                    expires: Date.now() + (3300 * 1000),
                    preAuth,
                    ext: newPhotoData.ext,
                    retry: 0
                  })
                }
              })
              .catch(err => {
                log.error('onNewSeries:: Graphql Error', err)
              })
          },
          onDelete: () => {
            onAfterSeriesDelete() // UI Stuff, just bring us back to the feed
            return deleteSeriesMutation()
              .catch(err => {
                log.error('onDelete:: Graphql Error', err)
              })
          },
          onClose: globalActions.onMenuBack,
          onFindOnMap: onFindOnMap,
          onWatch: signedIn ? () => watchSeriesMutation(currentUser.id) : null,
          onUnWatch: signedIn ? () => unWatchSeriesMutation(currentUser.id) : null
        }}

      />
    )
  }
  InnerSeries.propTypes = {
    domain: PropTypes.string,
    activeSeries: PropTypes.string,
    initialPhoto: PropTypes.string,

    // From Redux
    onAfterSeriesDelete: PropTypes.func,
    queueUpload: PropTypes.func,

    // Data from apollo
    series: PropTypes.object,

    // Functions From Apollo
    loading: PropTypes.bool,

    updateSeriesMutation: PropTypes.func,
    updatePhotoMutation: PropTypes.func,
    deleteSeriesMutation: PropTypes.func,
    deletePhotoMutation: PropTypes.func,
    createSeriesMutation: PropTypes.func,
    createPhotoMutation: PropTypes.func,
    watchSeriesMutation: PropTypes.func,
    unWatchSeriesMutation: PropTypes.func,
    onFindOnMap: PropTypes.func
  }
  return InnerSeries
}

/**
 * Here's the graphql HOC that gets an individual series
 */
const withGetSelectedQuery = graphql(graphqlAPI.queries.getWholeSeries, {
  props: ({ data }) => {
    if (data.error) log.error(data.error)
    // log.info('getWholeSeries:: ', { data })
    let series = null
    try {
      if (!data.loading && !data.error) series = data.series
    }
    catch (e) {
      log.info('getWholeSeries:: error', { data, e })
      log.error(e)
    }
    return {
      data,
      loading: data.loading,
      series
    }
  },
  skip: ({ activeSeries, isNewSeries }) => !activeSeries || isNewSeries,
  options: ({ activeSeries }) => ({
    variables: {
      id: activeSeries
    }
  })
})

/**
 * Map our redux state and functions
 */
const mapStateToProps = (state, props) => {
  const { current: { activeSeries, initialPhoto, domain } } = state.home
  return { activeSeries, initialPhoto, domain, ...props }
}

const mapDispatchToProps = dispatch => ({
  onAfterSeriesDelete: () => {
    log.info('onAfterSeriesDelete')
    dispatch(datastore.redux.actions.setHomeState(STATES.FEED, { activeSeries: null }))
  },
  queueUpload: (uploadObj) => {
    dispatch(datastore.redux.actions.queueUpload(uploadObj))
  },
  // activeSeries is an id
  doneCreatingSeries: (activeSeries) => {
    log.info('doneCreatingSeries', activeSeries)
    dispatch(datastore.redux.actions.setHomeState(STATES.SERIES, { activeSeries }))
  },
  // We need to swap out the L-dfgsdfgsdfg id on photos
  doneCreatingPhoto: (initialPhoto) => {
    log.info('doneCreatingPhoto', initialPhoto)
    dispatch(datastore.redux.actions.setHomeState(STATES.SERIES, { initialPhoto }))
  },
  onFindOnMap: () => {
    dispatch(datastore.redux.actions.setHomeState(STATES.MAP))
  }
})

/**
 * Compose all our queries together. This is a bit of a mess but kind of necessary for now
 */
export default compose(
  // connect first so that we can retrieve our seriesId
  connect(
    mapStateToProps,
    mapDispatchToProps
  ),
  withGetSelectedQuery,
  datastore.graphql.mutations.createSeries.withMutation,
  datastore.graphql.mutations.createPhoto.withMutation,
  datastore.graphql.mutations.updateSeries.withMutation,
  datastore.graphql.mutations.updatePhoto.withMutation,
  datastore.graphql.mutations.deleteSeries.withMutation,
  datastore.graphql.mutations.deletePhoto.withMutation,
  datastore.graphql.mutations.watch.withMutation,
  datastore.graphql.mutations.unwatch.withMutation,
  withSeriesConnect
)
