import React, { useMemo, useContext, useEffect } from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import { graphql, compose } from 'react-apollo'
import datastore from '@morphoto/datastore'
import { graphqlAPI } from '@morphoto/common'
import { AppContext } from '@morphoto/client-common'
import log from 'loglevel'

const withUploadConnect = Wrapped => {
  const Inner = ({ activeUpload, uploading, uploadOnline, updateUploadProgress, setUploaderOnline }) => {
    const { online: { isOnline } } = useContext(AppContext)
    // log.info('withUploadConnect::: RENDER', { isOnline, uploadOnline })

    useEffect(() => {
      // log.info('withUploadConnect:::', { uploadOnline, isOnline })
      if (uploadOnline !== isOnline) setUploaderOnline(isOnline)
    }, [isOnline, setUploaderOnline, uploadOnline])

    // This lurker waits for the s3 credentials to expire then it goes to get new ones.
    const newS3Creds = useMemo(() => <NewS3Credentials onDone={updateUploadProgress} activeUpload={activeUpload} />,
      [updateUploadProgress, activeUpload])

    if (!activeUpload || !uploading) return newS3Creds
    return (
      <React.Fragment>
        {newS3Creds}
        <Wrapped text={'Uploading image'} progress={activeUpload.progress || 0} />
      </React.Fragment>
    )
  }
  Inner.propTypes = {
  // All from redux
    activeUpload: PropTypes.object,
    uploading: PropTypes.bool,
    uploadOnline: PropTypes.bool,
    updateUploadProgress: PropTypes.func.isRequired,
    setUploaderOnline: PropTypes.func.isRequired
  }
  return Inner
}

/**
 * Map our redux state and functions
 */
const mapStateToProps = (state, props) => {
  const { activeUpload, uploading, online: uploadOnline } = state.uploader
  return { activeUpload, uploading, uploadOnline }
}

const mapDispatchToProps = dispatch => ({
  updateUploadProgress: (newProgress) => {
    dispatch(datastore.redux.actions.updateUploadProgress(newProgress))
  },
  setUploaderOnline: (bOnline) => {
    dispatch(datastore.redux.actions.setUploaderOnline(bOnline))
  }
})

export default compose(
  connect(mapStateToProps, mapDispatchToProps),
  withUploadConnect
)

/**
 * Here's the graphql HOC that gets an individual series
 */
const withGetNewS3Credentials = graphql(graphqlAPI.queries.getNewS3Credentials, {
  skip: ({ online, activeUpload }) => !online || !activeUpload || (activeUpload.expires > Date.now()),
  props: ({ data, ownProps }) => {
    if (data.error) log.error(data.error)
    log.info('withGetNewS3Credentials:: ', { data })

    // If we get backa  valid auth code then create a new queue object
    // and send it back
    if (!data.loading) {
      ownProps.onDone({
        ...ownProps.qItem,
        retry: ownProps.qItem.retry + 1,
        preAuth: JSON.parse(data.s3Credentials)
      })
    }
    return {
      data
    }
  },
  options: ({ qItem }) => ({
    variables: { seriesId: qItem.seriesId, photoId: qItem.id, ext: qItem.ext }
  })
})
const NewS3Credentials = withGetNewS3Credentials(() => null)
