import React, { useContext, useEffect } from 'react'
import PropTypes from 'prop-types'
import ReactGA from 'react-ga'

import { graphql, compose } from 'react-apollo'
// import { print } from 'graphql/language/printer'

import { AppContext } from '@morphoto/client-common'
import { graphqlAPI } from '@morphoto/common'

const withProfileConnect = Wrapped => {
  const Inner = ({ data }) => {
    const { seriesQ, watchedSeriesQ } = data
    const { currentUser, globalActions } = useContext(AppContext)

    const mySeries = seriesQ && seriesQ.userSeries && seriesQ.userSeries.seriesSummaries
      ? seriesQ.userSeries.seriesSummaries.filter(s => s.seriesStatus !== 'deleted') || []
      : []
    const watchingSeries = watchedSeriesQ && watchedSeriesQ.arraySeries ? watchedSeriesQ.arraySeries : []
    // log.info('ProfileConnect', { mySeries, watchedSeriesQ, watchingSeries })

    const loading = Boolean(
      (seriesQ && seriesQ.loading) ||
    (watchedSeriesQ && watchedSeriesQ.loading)
    )

    useEffect(() => {
      ReactGA.pageview('profile')
    }, [])

    return <Wrapped user={currentUser} {...{
      loading,
      mySeries,
      watchingSeries,
      actions: {
        jumpToSeries: globalActions.jumpToSeriesView,
        jumpToMap: globalActions.jumpToSeriesMap
      }
    }} />
  }

  Inner.propTypes = {
    data: PropTypes.object, // from graphql
    jumpToSeries: PropTypes.func, // from redux
    jumpToMap: PropTypes.func // from redux
  }
  return Inner
}

const withuserSeriesQuery = graphql(graphqlAPI.queries.userSeries, {
  props: ({ data, ownProps }) => {
    // print(graphqlAPI.queries.userSeries)
    // log.info('ProfileConnect withuserSeriesQuery', { data, ownProps })
    return { data: { ...ownProps.data, seriesQ: data } }
  }
})

const withMyWatchedSeriesQuery = graphql(graphqlAPI.queries.seriesFromList, {
  props: ({ data, ownProps }) => {
    // log.info('ProfileConnect withMyWatchedSeriesQuery', graphqlAPI.queries.seriesFromList, { data, ownProps })
    return { data: { ...ownProps.data, watchedSeriesQ: data } }
  },
  skip: ({ data, currentUser }) => {
    try {
      return currentUser.watching.length < 1
    }
    catch (e) {
      return true
    }
  },
  options: ({ data, currentUser }) => {
    return {
      variables: { seriesList: currentUser.watching }
    }
  }
})

export default compose(
  withuserSeriesQuery,
  // withMyPhotosQuery,
  withMyWatchedSeriesQuery,
  withProfileConnect
)
